"use client"

import { useState, useEffect, useRef } from "react"
import { ArrowLeft, RotateCcw } from "lucide-react"
import * as THREE from "three"
import { useThreeScene } from "@/hooks/use-three-scene"
import { useMousePosition } from "@/hooks/use-mouse-position"
import { createGameLights, createSphereMesh } from "@/lib/three-helpers"
import { UI_TEXT, GAME_CONFIG } from "@/lib/constants"

interface NextNumberGameProps {
  onBack: () => void
}

interface Question {
  sequence: number[]
  options: number[]
  correct: number
}

const generateQuestions = (): Question[] => {
  return [
    {
      sequence: [2, 4, 6, 8],
      options: [9, 10, 11, 12],
      correct: 10,
    },
    {
      sequence: [3, 6, 9, 12],
      options: [14, 15, 16, 17],
      correct: 15,
    },
    {
      sequence: [1, 1, 2, 3, 5],
      options: [7, 8, 9, 10],
      correct: 8,
    },
    {
      sequence: [1, 2, 4, 8],
      options: [15, 16, 17, 18],
      correct: 16,
    },
    {
      sequence: [1, 4, 9, 16],
      options: [24, 25, 26, 27],
      correct: 25,
    },
  ]
}

export default function NextNumberGame({ onBack }: NextNumberGameProps) {
  const [questions] = useState<Question[]>(generateQuestions())
  const [currentQuestion, setCurrentQuestion] = useState(0)
  const [score, setScore] = useState(0)
  const [answered, setAnswered] = useState(false)
  const [selectedAnswer, setSelectedAnswer] = useState<number | null>(null)
  const canvasRef = useRef<HTMLDivElement>(null)
  const mouseRef = useMousePosition(canvasRef)
  const groupsRef = useRef<THREE.Group[]>([])

  useThreeScene({
    containerRef: canvasRef,
    cameraPosition: { z: 30 },
    enableShadows: true,
    onAnimate: (scene, camera, renderer) => {
      const time = Date.now() * 0.001

      groupsRef.current.forEach((group) => {
        if (group.userData.number !== undefined) {
          // Rotation
          group.rotation.x += GAME_CONFIG.ANIMATION.ROTATION_SPEED
          group.rotation.y += GAME_CONFIG.ANIMATION.ROTATION_SPEED

          // Floating animation
          group.position.y = group.userData.targetY + Math.sin(time + group.userData.index) * GAME_CONFIG.ANIMATION.FLOAT_AMPLITUDE
          group.position.z = Math.sin(time * 0.5 + group.userData.index) * 4

          // Mouse influence
          group.position.x += (mouseRef.current.x * 5 - group.position.x * 0.02) * GAME_CONFIG.ANIMATION.MOUSE_INFLUENCE

          // Pulsing torus ring
          if (group.userData.torusRing) {
            const pulsing = Math.sin(time * GAME_CONFIG.ANIMATION.PULSE_SPEED + group.userData.index) * 0.5 + 0.5
            group.userData.torusRing.scale.z = 1 + pulsing * 0.3
            group.userData.torusRing.material.opacity = pulsing * 0.8 + 0.2
          }
        }
      })
    },
  })

  useEffect(() => {
    if (!canvasRef.current) return

    // This effect will recreate the scene when question changes
    // The actual scene setup is handled by useThreeScene hook
    const container = canvasRef.current
    const scene = new THREE.Scene()
    scene.fog = new THREE.Fog(
      GAME_CONFIG.THREE.BACKGROUND_COLOR,
      GAME_CONFIG.THREE.FOG_NEAR,
      GAME_CONFIG.THREE.FOG_FAR,
    )

    const camera = new THREE.PerspectiveCamera(
      GAME_CONFIG.THREE.FOV,
      container.clientWidth / container.clientHeight,
      GAME_CONFIG.THREE.NEAR,
      GAME_CONFIG.THREE.FAR,
    )
    camera.position.z = 30

    const renderer = new THREE.WebGLRenderer({ antialias: true, alpha: true })
    renderer.setSize(container.clientWidth, container.clientHeight)
    renderer.setClearColor(GAME_CONFIG.THREE.BACKGROUND_COLOR, 0)
    renderer.outputColorSpace = THREE.SRGBColorSpace
    renderer.shadowMap.enabled = true
    renderer.shadowMap.type = THREE.PCFShadowMap
    container.appendChild(renderer.domElement)

    const question = questions[currentQuestion]
    const groups: THREE.Group[] = []

    // Create spheres for sequence
    question.sequence.forEach((num, idx) => {
      const group = new THREE.Group()

      // Create sphere
      const hue = idx / question.sequence.length
      const color = new THREE.Color().setHSL(hue, 0.8, 0.5)
      const sphere = createSphereMesh(1.2, color, {
        segments: 64,
        metalness: 0.3,
        roughness: 0.4,
        emissiveIntensity: 0.5,
      })
      group.add(sphere)

      // Add glowing torus ring
      const torusGeom = new THREE.TorusGeometry(1.8, 0.15, 32, 100)
      const torusMat = new THREE.MeshBasicMaterial({
        color: new THREE.Color().setHSL(hue, 0.9, 0.6),
        transparent: true,
        opacity: 0.8,
      })
      const torus = new THREE.Mesh(torusGeom, torusMat)
      torus.userData.originalOpacity = 0.8
      group.add(torus)

      // Position in circle
      const angle = (idx / question.sequence.length) * Math.PI * 2
      group.position.x = Math.cos(angle) * 12
      group.position.y = Math.sin(angle) * 12
      group.userData = {
        targetX: group.position.x,
        targetY: group.position.y,
        number: num,
        index: idx,
        torusRing: torus,
      }

      scene.add(group)
      groups.push(group)
    })

    groupsRef.current = groups

    // Setup lighting
    createGameLights(scene)

    // Animation loop
    const animate = () => {
      requestAnimationFrame(animate)
      const time = Date.now() * 0.001

      groups.forEach((group) => {
        if (group.userData.number !== undefined) {
          group.rotation.x += GAME_CONFIG.ANIMATION.ROTATION_SPEED
          group.rotation.y += GAME_CONFIG.ANIMATION.ROTATION_SPEED

          group.position.y = group.userData.targetY + Math.sin(time + group.userData.index) * GAME_CONFIG.ANIMATION.FLOAT_AMPLITUDE
          group.position.z = Math.sin(time * 0.5 + group.userData.index) * 4

          group.position.x += (mouseRef.current.x * 5 - group.position.x * 0.02) * GAME_CONFIG.ANIMATION.MOUSE_INFLUENCE

          if (group.userData.torusRing) {
            const pulsing = Math.sin(time * GAME_CONFIG.ANIMATION.PULSE_SPEED + group.userData.index) * 0.5 + 0.5
            group.userData.torusRing.scale.z = 1 + pulsing * 0.3
            group.userData.torusRing.material.opacity = pulsing * 0.8 + 0.2
          }
        }
      })

      renderer.render(scene, camera)
    }

    animate()

    const handleResize = () => {
      if (container) {
        camera.aspect = container.clientWidth / container.clientHeight
        camera.updateProjectionMatrix()
        renderer.setSize(container.clientWidth, container.clientHeight)
      }
    }

    window.addEventListener("resize", handleResize)

    return () => {
      window.removeEventListener("resize", handleResize)

      // Cleanup
      groups.forEach((group) => {
        group.traverse((object) => {
          if (object instanceof THREE.Mesh) {
            object.geometry.dispose()
            if (Array.isArray(object.material)) {
              object.material.forEach((material) => material.dispose())
            } else {
              object.material.dispose()
            }
          }
        })
        scene.remove(group)
      })

      renderer.dispose()
      if (container.contains(renderer.domElement)) {
        container.removeChild(renderer.domElement)
      }
    }
  }, [currentQuestion, questions, mouseRef])

  const handleAnswer = (option: number) => {
    if (answered) return

    setSelectedAnswer(option)
    setAnswered(true)

    if (option === questions[currentQuestion].correct) {
      setScore((prev) => prev + 1)
    }
  }

  const handleNext = () => {
    if (currentQuestion < questions.length - 1) {
      setCurrentQuestion((prev) => prev + 1)
      setAnswered(false)
      setSelectedAnswer(null)
    }
  }

  const handleRestart = () => {
    setCurrentQuestion(0)
    setScore(0)
    setAnswered(false)
    setSelectedAnswer(null)
  }

  const isGameComplete = currentQuestion === questions.length - 1 && answered
  const question = questions[currentQuestion]

  return (
    <div className="relative w-full h-full flex flex-col">
      <div ref={canvasRef} className="absolute inset-0" />

      <div className="absolute top-6 left-6 flex gap-4 z-10">
        <button
          onClick={onBack}
          className="flex items-center gap-2 px-4 py-2 bg-blue-600 hover:bg-blue-700 text-white rounded-lg transition-all duration-300"
          aria-label={UI_TEXT.COMMON.BACK}
        >
          <ArrowLeft size={20} />
          {UI_TEXT.COMMON.BACK}
        </button>
        <button
          onClick={handleRestart}
          className="flex items-center gap-2 px-4 py-2 bg-purple-600 hover:bg-purple-700 text-white rounded-lg transition-all duration-300"
          aria-label={UI_TEXT.COMMON.RESTART}
        >
          <RotateCcw size={20} />
          {UI_TEXT.COMMON.RESTART}
        </button>
      </div>

      <div className="absolute top-6 right-6 z-10">
        <div className="text-2xl font-bold text-yellow-300 glow">
          ‚≠ê {UI_TEXT.COMMON.SCORE}: {score}/{questions.length}
        </div>
      </div>

      <div className="absolute inset-0 flex flex-col items-center justify-center pointer-events-none">
        <div
          className="bg-slate-900/80 backdrop-blur-md p-8 rounded-2xl max-w-2xl pointer-events-auto border-2 border-blue-500/30"
          style={{
            boxShadow: "0 0 30px rgba(59, 130, 246, 0.3), inset 0 0 20px rgba(59, 130, 246, 0.1)",
          }}
        >
          <div className="mb-8">
            <p className="text-sm text-blue-300 mb-3">
              C√¢u {currentQuestion + 1}/{questions.length}
            </p>
            <p className="text-xl font-bold text-white mb-6">{UI_TEXT.NEXT_NUMBER.TITLE}</p>

            <div className="flex gap-4 justify-center items-center mb-8 flex-wrap">
              {question.sequence.map((num, idx) => (
                <div key={`${num}-${idx}`} className="float-animation">
                  <div className="w-16 h-16 bg-gradient-to-br from-blue-500 to-blue-700 rounded-lg flex items-center justify-center text-2xl font-bold text-white glow">
                    {num}
                  </div>
                </div>
              ))}
              <div className="text-2xl text-gray-400 font-bold">?</div>
            </div>
          </div>

          {!answered ? (
            <div className="space-y-4">
              <p className="text-sm text-gray-300 mb-4">{UI_TEXT.NEXT_NUMBER.SELECT_ANSWER}</p>
              <div className="grid grid-cols-2 gap-4">
                {question.options.map((option) => (
                  <button
                    key={option}
                    onClick={() => handleAnswer(option)}
                    className="py-3 px-4 bg-gradient-to-r from-purple-600 to-pink-600 hover:from-purple-500 hover:to-pink-500 text-white font-bold rounded-lg transition-all duration-300 transform hover:scale-105 pulse-glow"
                    aria-label={`Ch·ªçn ƒë√°p √°n ${option}`}
                  >
                    {option}
                  </button>
                ))}
              </div>
            </div>
          ) : (
            <div className="text-center">
              {selectedAnswer === question.correct ? (
                <div className="mb-6">
                  <div className="text-6xl mb-4">üéâ</div>
                  <p className="text-2xl font-bold text-green-400 mb-2">{UI_TEXT.NEXT_NUMBER.CORRECT}</p>
                  <p className="text-gray-300">{UI_TEXT.NEXT_NUMBER.CORRECT_DESC}</p>
                </div>
              ) : (
                <div className="mb-6">
                  <div className="text-6xl mb-4">üòä</div>
                  <p className="text-2xl font-bold text-orange-400 mb-2">{UI_TEXT.NEXT_NUMBER.INCORRECT}</p>
                  <p className="text-gray-300">
                    {UI_TEXT.NEXT_NUMBER.CORRECT_ANSWER}{" "}
                    <span className="font-bold text-green-400">{question.correct}</span>
                  </p>
                </div>
              )}

              {!isGameComplete && (
                <button
                  onClick={handleNext}
                  className="mt-6 px-6 py-2 bg-green-600 hover:bg-green-700 text-white font-bold rounded-lg transition-all duration-300"
                >
                  {UI_TEXT.COMMON.NEXT_QUESTION}
                </button>
              )}
            </div>
          )}

          {isGameComplete && (
            <div className="text-center mt-6">
              <p className="text-xl text-gray-300 mb-4">
                {UI_TEXT.NEXT_NUMBER.COMPLETE} {score} {UI_TEXT.NEXT_NUMBER.POINTS}
              </p>
              <button
                onClick={handleRestart}
                className="px-6 py-2 bg-blue-600 hover:bg-blue-700 text-white font-bold rounded-lg transition-all duration-300"
              >
                {UI_TEXT.COMMON.PLAY_AGAIN}
              </button>
            </div>
          )}
        </div>
      </div>
    </div>
  )
}
