"use client"

import { useState } from "react"
import GameMenu from "@/components/game-menu"
import NextNumberGame from "@/components/games/next-number-game"
import MissingNumberGame from "@/components/games/missing-number-game"
import SortSequenceGame from "@/components/games/sort-sequence-game"
import SolarSystemBackground from "@/components/solar-system-background"
import { GAME_TYPES } from "@/lib/constants"
import type { GameType } from "@/lib/constants"

export default function Home() {
  const [currentGame, setCurrentGame] = useState<GameType>(GAME_TYPES.MENU)

  const handleBackToMenu = () => {
    setCurrentGame(GAME_TYPES.MENU)
  }

  const handleSelectGame = (game: Exclude<GameType, "menu">) => {
    setCurrentGame(game)
  }

  const renderContent = () => {
    switch (currentGame) {
      case GAME_TYPES.NEXT:
        return <NextNumberGame onBack={handleBackToMenu} />
      case GAME_TYPES.MISSING:
        return <MissingNumberGame onBack={handleBackToMenu} />
      case GAME_TYPES.SORT:
        return <SortSequenceGame onBack={handleBackToMenu} />
      case GAME_TYPES.MENU:
      default:
        return <GameMenu onSelectGame={handleSelectGame} />
    }
  }

  return (
    <>
      <SolarSystemBackground />
      <main className="w-full h-screen flex items-center justify-center overflow-hidden relative z-10">
        {renderContent()}
      </main>
    </>
  )
}
