"use client"

import { useEffect, useRef } from "react"
import * as THREE from "three"

export default function SolarSystemBackground() {
  const containerRef = useRef<HTMLDivElement>(null)

  useEffect(() => {
    if (!containerRef.current) return

    const width = window.innerWidth
    const height = window.innerHeight

    const scene = new THREE.Scene()
    const camera = new THREE.PerspectiveCamera(75, width / height, 0.1, 10000)
    const renderer = new THREE.WebGLRenderer({ antialias: true, alpha: true })

    camera.position.z = 90
    renderer.setSize(width, height)
    renderer.setClearColor(0x0a0e27, 1)
    renderer.outputColorSpace = THREE.SRGBColorSpace
    containerRef.current.appendChild(renderer.domElement)

    // Stars background with more density
    const starsGeometry = new THREE.BufferGeometry()
    const starCount = 1500
    const starPositions = new Float32Array(starCount * 3)
    const starSizes = new Float32Array(starCount)

    for (let i = 0; i < starCount * 3; i += 3) {
      starPositions[i] = (Math.random() - 0.5) * 2000
      starPositions[i + 1] = (Math.random() - 0.5) * 2000
      starPositions[i + 2] = (Math.random() - 0.5) * 2000
      starSizes[i / 3] = Math.random() * 1.5
    }

    starsGeometry.setAttribute("position", new THREE.BufferAttribute(starPositions, 3))
    starsGeometry.setAttribute("size", new THREE.BufferAttribute(starSizes, 1))

    const starsMaterial = new THREE.PointsMaterial({
      color: 0xffffff,
      sizeAttenuation: true,
      transparent: true,
      opacity: 0.8,
    })
    const stars = new THREE.Points(starsGeometry, starsMaterial)
    scene.add(stars)

    // Sun with enhanced glow
    const sunGeometry = new THREE.SphereGeometry(10, 64, 64)
    const sunMaterial = new THREE.MeshBasicMaterial({
      color: 0xfdb813,
      transparent: true,
      opacity: 1,
    })
    const sun = new THREE.Mesh(sunGeometry, sunMaterial)
    scene.add(sun)

    // Sun glow layers
    const glowGeometry1 = new THREE.SphereGeometry(11, 32, 32)
    const glowMaterial1 = new THREE.MeshBasicMaterial({
      color: 0xffa500,
      transparent: true,
      opacity: 0.3,
    })
    const sunGlow1 = new THREE.Mesh(glowGeometry1, glowMaterial1)
    scene.add(sunGlow1)

    const glowGeometry2 = new THREE.SphereGeometry(13, 32, 32)
    const glowMaterial2 = new THREE.MeshBasicMaterial({
      color: 0xff8c00,
      transparent: true,
      opacity: 0.15,
    })
    const sunGlow2 = new THREE.Mesh(glowGeometry2, glowMaterial2)
    scene.add(sunGlow2)

    // Planets data with better colors
    const planets = [
      { name: "Mercury", size: 3, distance: 25, speed: 0.08, color: 0x8c7853 },
      { name: "Venus", size: 4, distance: 38, speed: 0.03, color: 0xffc649 },
      { name: "Earth", size: 3.5, distance: 52, speed: 0.018, color: 0x2e7cde },
      { name: "Mars", size: 3, distance: 67, speed: 0.012, color: 0xe27b58 },
    ]

    const planetMeshes = planets.map((planet) => {
      // Orbit line
      const orbitGeometry = new THREE.BufferGeometry()
      const orbitPoints = []
      for (let i = 0; i <= 128; i++) {
        const angle = (i / 128) * Math.PI * 2
        orbitPoints.push(Math.cos(angle) * planet.distance, 0, Math.sin(angle) * planet.distance)
      }
      orbitGeometry.setAttribute("position", new THREE.BufferAttribute(new Float32Array(orbitPoints), 3))
      const orbitMaterial = new THREE.LineBasicMaterial({
        color: 0x4a5568,
        transparent: true,
        opacity: 0.4,
        linewidth: 1,
      })
      const orbit = new THREE.Line(orbitGeometry, orbitMaterial)
      scene.add(orbit)

      // Planet
      const geometry = new THREE.SphereGeometry(planet.size, 32, 32)
      const material = new THREE.MeshStandardMaterial({
        color: planet.color,
        roughness: 0.6,
        metalness: 0.2,
        emissive: planet.color,
        emissiveIntensity: 0.1,
      })
      const mesh = new THREE.Mesh(geometry, material)
      mesh.castShadow = true
      mesh.userData = { angle: Math.random() * Math.PI * 2, ...planet }
      scene.add(mesh)
      return mesh
    })

    // Enhanced lighting
    const ambientLight = new THREE.AmbientLight(0xffffff, 0.4)
    scene.add(ambientLight)

    const sunLight = new THREE.PointLight(0xfdb813, 2, 250)
    sunLight.position.copy(sun.position)
    scene.add(sunLight)

    // Additional accent lights
    const accentLight1 = new THREE.PointLight(0x00d9ff, 1, 150)
    accentLight1.position.set(-100, 50, 50)
    scene.add(accentLight1)

    const accentLight2 = new THREE.PointLight(0xff006e, 0.8, 150)
    accentLight2.position.set(100, -50, 50)
    scene.add(accentLight2)

    // Mouse tracking for interactivity
    let mouseX = 0
    let mouseY = 0

    const handleMouseMove = (e: MouseEvent) => {
      mouseX = (e.clientX / width) * 2 - 1
      mouseY = -(e.clientY / height) * 2 + 1
    }

    window.addEventListener("mousemove", handleMouseMove)

    const handleResize = () => {
      const newWidth = window.innerWidth
      const newHeight = window.innerHeight
      camera.aspect = newWidth / newHeight
      camera.updateProjectionMatrix()
      renderer.setSize(newWidth, newHeight)
    }

    window.addEventListener("resize", handleResize)

    // Animation loop
    const animate = () => {
      requestAnimationFrame(animate)

      // Rotate sun
      sun.rotation.y += 0.003
      sunGlow1.rotation.y -= 0.002
      sunGlow2.rotation.y += 0.001

      // Pulsing sun glow
      const time = Date.now() * 0.001
      sunGlow1.scale.set(1 + Math.sin(time * 1.5) * 0.1, 1 + Math.sin(time * 1.5) * 0.1, 1 + Math.sin(time * 1.5) * 0.1)

      // Animate planets
      planetMeshes.forEach((mesh) => {
        mesh.userData.angle += mesh.userData.speed
        mesh.position.x = Math.cos(mesh.userData.angle) * mesh.userData.distance
        mesh.position.z = Math.sin(mesh.userData.angle) * mesh.userData.distance
        mesh.rotation.y += 0.015

        // Slight y wobble
        mesh.position.y = Math.sin(mesh.userData.angle * 0.5) * 2
      })

      // Subtle camera movement based on mouse
      camera.position.x += (mouseX * 15 - camera.position.x) * 0.02
      camera.position.y += (mouseY * 15 - camera.position.y) * 0.02
      camera.lookAt(scene.position)

      // Rotate stars slowly
      stars.rotation.y += 0.00002

      renderer.render(scene, camera)
    }

    animate()

    return () => {
      window.removeEventListener("resize", handleResize)
      window.removeEventListener("mousemove", handleMouseMove)
      containerRef.current?.removeChild(renderer.domElement)
    }
  }, [])

  return <div ref={containerRef} className="fixed inset-0 -z-10" />
}
